<template>
  <v-container grid-list-md>
    <v-layout row fill-height>
      <v-flex xs12>
        <div>
          <h3 class="headline mb-0">Drop files anywhere to upload</h3>
          <div class="upload">

            <div v-show="$refs.upload && $refs.upload.dropActive" class="drop-active">
