            </div>

            <div class="upload-btn">
              <vue-upload-component
                extensions="csv"
                accept="text/csv"
                :custom-action="onFilePick"
                :multiple="false"
                :drop="true"
                :drop-directory="false"
                @input-file="inputFile"
                v-model="files"
                ref="upload">
                <v-icon x-large color="green lighten-1">cloud_upload</v-icon>
              </vue-upload-component>
            </div>
          </div>
        </div>
